*{
  margin: 0px; 
  padding: 0px;
  box-sizing: border-box;
}

html{
  margin: 0px; 
  padding: 0px;

}


body{  
 
  background-image: linear-gradient(35deg,
  rgb(0, 0, 0,0.5), rgb(0, 0, 0,0.5)), url("./media/toDolist.jpeg");
    background-size: 100%;
  


}



.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 500px;
  margin: 0px auto;
  /* background:linear-gradient(90deg, #341212 0%, #3e3e54 35%, #205c69 100%); */
  background-color: rgba(0, 0, 0, 0.7);
  position: relative;
  top:50px;
  border: 2.5px solid ;
  border-top-left-radius: 50px;
  border-top-right-radius: 43px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 43px;
  /* box-shadow: 18px 18px 10px 0px rgba(255, 10, 10, 0.2); */
}




header{
     background-color: rgba(255, 206, 100, 0.8);
    width: 100%;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    border-top-left-radius: 50px;
    border-top-right-radius: 43px;
    
    font-size: 18px;
    
}
main{

  width: 100%;
  display:flex;
  flex-direction:column;
  justify-content: center; 
  align-items: center;
 

}
ul{
  width: 100%;
}

.li-tiem{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 0 .5rem .5rem ;
  margin: 10px;
  background-color: rgb(255, 255, 255);
  font-size: 20px;
}

.li-tiem input[type="checkbox"] {
  width: 2.5rem;
  height: 2.8rem;
  cursor: pointer;
  margin-right: 0.8rem;
}

label:hover{
  /* color:rgb(181, 172, 172); */
  text-decoration: underline;
  color: rgb(11, 2, 2,0.7);

}

/* .li-tiem svg:hover{
  color:red;
  font-size: 25px;
} */

.li-tiem svg{
  font-size: 30px;
  color: rgb(213, 10, 10);
  cursor: pointer;
}

label{
  padding: 0.4rem;
  color: rgb(11, 2, 2);
  font-size: 30px;
  cursor: pointer;
  
}

.addlist-input{
  padding: 10PX;
  width: 340px;
}

.addlist-input ::placeholder{
  color: darkgrey;
}

.serchlist{
  padding: 10PX;
  width: 340px;
  margin-bottom: 9px;
  
}

.delbut{ 
   margin-right: 22px;
  
}


footer{
  background-color: rgb(255, 206, 100, 0.8);
  width: 100%;
  padding: 1em;
  display: flex;
  flex-grow: 1;
  align-content: center;
  justify-content:center;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 43px;
  
}  

.add-list{
  width: 340px;
    display: flex;
    margin-bottom: 15px;
    margin-top: 15px;
    

}


.list-count{
  font-size: 30px;
  font-weight: 900;
  color: whitesmoke;
}


.add-button{
  margin: 10px;
font-size: 18px;
cursor: pointer;
}

.add-button-out{
  background-color: rgb(58, 198, 15);
}
 
.empt-list{
  font-size: 30px;
  font-weight: 900;
  color: aliceblue;
  padding: 30px;
}